import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';
import ProductItem from '../../shop/container/products/ProductItem';

class HomeProducts extends Component {
    componentDidMount() {
        this.props.getHomeProducts()
    }
    render() {
        const { authentication: { isAuthenticated }, products: { homeProductsError, homeProductsLoader, homeProducts }, } = this.props;

        return (
            <section id="home_products" className="home_products">
                <div className="container">
                    {
                        homeProductsError &&
                        <div className="d-flex justify-content-center">
                            <div className="col-lg-6 col-md-8 col-sm-12">
                                <div className="alert alert-danger">{homeProductsError}</div>
                            </div>
                        </div>
                    }
                    {homeProductsLoader && <div id="preloader" />}
                    {homeProducts && homeProducts.length > 0 &&
                        <React.Fragment>
                            <div className="section-header">
                                <h2>Trending products</h2>
                            </div>
                            <div className="row gy-4">
                                {homeProducts.slice(0, 8).map(product => <ProductItem product={product} func={this.addToCartMethod} isAuthenticated={isAuthenticated} key={product.id} className={`col-lg-3 col-md-4 col-sm-6`} />)}
                            </div>
                            <div className="text-center">
                                <div className="container">
                                    <Link to="/shop/products/" className="btn btn-primary px-lg-4">More</Link>
                                </div>
                            </div>
                        </React.Fragment>
                    }
                </div>
            </section>
        )
    }
}



export default HomeProducts;