import React, { Component } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';
import redux from '../../ReduxMapper';
import { connect } from 'react-redux';
import Api, { APICall } from '../../Api';
import Utils from '../../utils/utils';
import component from '../../Components';
import ProductInfo from './product/ProductInfo';
import FacebookShare from '../../social/FacebookShare';
import TwitterShare from '../../social/TwitterShare';




/**
*
* @mwangihub
* @summary Cart Component
* @class ProductChild
* @extends {Component}
* @param {object} props From redux => redux Mapper
* @return {JSX} return JSX <CartItemRow/>  
*
*/
class ProductChild extends Component {
    constructor(props) {
        super(props)
        this.state = {
            product: null,
            loading: false,
            addTocartLoader: false,
            error: null,
            quantity: null,
            reviews: null
        }
    }

    fetchProduct = id => {
        this.setState({ loading: false })
        Api.itemDetail(id)
            .then(res => {
                const { cart_count, reviews } = res.data;
                this.setState({ loading: false, product: res.data, quantity: cart_count, reviews: reviews })
            })
            .catch(error => {
                if (error.response) {
                    let res = error.response;
                    this.setState({ loading: false, error: res })
                } else if (error.request) {
                    console.log(error.request);
                    this.setState({ loading: false, error: error.request })
                } else {
                    this.setState({ loading: false, error: "Another error occured while fetching a product" })
                    console.log("Another error occured while fetching a product")
                }
            })
    }

    increaseQuantity = slug => {
        return APICall(Api.addItemsToCart(slug), response => {
            if (response.data.item) {
                this.props.localCartUpdated(response.data.order);
                this.fetchProduct(this.props.productID);
            }
        })
    };

    decreaseQuantity = slug => {
        return APICall(Api.reduceFromCart(slug), response => {
            if (response.data.item) {
                this.props.localCartUpdated(response.data.order);
                this.fetchProduct(this.props.productID);
            }
        })
    };


    componentDidMount() {
        this.fetchProduct(this.props.productID)
    }
    render() {
        const { product, quantity, reviews } = this.state;
        const { authentication: { isAuthenticated }, localCartUpdated, localHomeProductsUpdate } = this.props



        return (
            <React.Fragment>
                {product &&
                    <component.BreadCrumb
                        object={product}
                        current={"Product"}
                        links={[{ name: 'Home', to: '/' }, { name: 'Shop', to: '/shop/products/' }]}
                    />
                }

                <section className='mb-5 pb-3' >
                    {product &&
                        <div className='container'>
                            <div className="row product__single">
                                <div className="col-lg-7 mb-1">
                                    <div className="row">
                                        <div className="col-lg-2"></div>
                                        <div className="col-lg-10">
                                            {product.item_images && product.item_images.length > 0 &&
                                                product.item_images.map((img, i) => {
                                                    if (img.current)
                                                        return (
                                                            <img src={img.image} className="img-fluid w-100" alt="product-img" key={i} />
                                                        )
                                                    return null
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-5 product-details pl-md-5">
                                    {/* <h3>{product.title}</h3> */}
                                    <div className="rating d-flex mt-4">
                                        {product.rates > 0 &&
                                            <p className="text-left me-4">
                                                <a href="/" className="me-2 text-decoration-none text-warning">{product.rates}</a>
                                                <a href="/" className='text-warning'><i className="bi bi-star-fill" /></a>
                                                <a href="/" className='text-warning'><i className="bi bi-star-fill" /></a>
                                                <a href="/" className='text-warning'><i className="bi bi-star-fill" /></a>
                                                <a href="/" className='text-warning'><i className="bi bi-star-fill" /></a>
                                            </p>
                                        }
                                        <p className="text-left me-4">
                                            <a href="/" className="me-2 text-dark text-decoration-none">{product.rating_count} <span className='text-muted'>Rating</span></a>
                                        </p>
                                        <p className="text-left d-none">
                                            <a href="/" className="me-2 text-black text-decoration-none" >500 <span className='text-muted'>Sold</span></a>
                                        </p>
                                        {quantity && <p className="text-left">
                                            <span className="badge bg-warning text-dark">This item is your cart</span>
                                        </p>}
                                    </div>
                                    <p className="price fs-2">
                                        <span>
                                            {product.discounted_price > 0 && <del className=" text-muted me-2">Kshs.{product.price}</del>}
                                            {product.discounted_price > 0 && <span className='fw-bold'>Kshs. {product.discounted_price}</span>}
                                            {product.discounted_price === 0 && <span className='fw-bold'>Kshs. {product.price}</span>}
                                        </span>
                                    </p>


                                    <div className="col-md-12">
                                        {product.stock > 0 && <p className='text-dark badge bg-info'>{product.stock} of this item available</p>}
                                        {product.stock < 1 && <p className='badge bg-secondary'>Out of stock</p>}
                                    </div>

                                    <div className="my-2 product__info">
                                        {isAuthenticated &&
                                            <React.Fragment>
                                                {quantity &&
                                                    <div className='d-flex flex-column'>
                                                        <div className="input-group col-md-6 mb-3">
                                                            <button className="btn btn-sm btn-outline-dark me-3 fw-bold" onClick={e => {
                                                                if (product.stock > 0) {
                                                                    this.increaseQuantity(product.slug)
                                                                }
                                                            }} disabled={product.stock < 1}>
                                                                <i className="fs-5 bi bi-plus" />
                                                            </button>
                                                            <button className="btn btn-sm btn-outline-secondary px-5 text-black fw-bold" disabled>{quantity}</button>
                                                            <button className="btn btn-sm btn-outline-dark ms-3 fw-bold" onClick={e => this.decreaseQuantity(product.slug)}>
                                                                <i className="fs-5 bi bi-dash" />
                                                            </button>
                                                        </div>
                                                        <div>
                                                            <button className="btn btn-outline-danger btn-sm"
                                                                onClick={e => APICall(Api.deleteFromCart(product.slug), response => {
                                                                    localCartUpdated(response.data.order);
                                                                    this.fetchProduct(product.slug);
                                                                })}
                                                            >Remove from cart</button>
                                                        </div>
                                                    </div>
                                                }
                                                {!quantity &&
                                                    <React.Fragment>
                                                        {product.stock > 0 ?
                                                            <form onSubmit={
                                                                e => {
                                                                    e.preventDefault();
                                                                    let size;
                                                                    if (e.target.size) {
                                                                        size = e.target.size.value
                                                                    }
                                                                    let quantity = parseInt(e.target.quantity.value);
                                                                    let slug = product.slug;
                                                                    APICall(Api.addToCart({ size, quantity, slug }), response => {
                                                                        if (response.data.item) {
                                                                            localCartUpdated(response.data.order);
                                                                            localHomeProductsUpdate(response.data.items);
                                                                            this.fetchProduct(product.slug)
                                                                        }
                                                                    })
                                                                }}>
                                                                <div className="row">
                                                                    {product.size_choices &&
                                                                        <div className="col">
                                                                            <select className="form-select form-select-sm" name='size'>
                                                                                <option defaultValue>Select size</option>
                                                                                <React.Fragment>
                                                                                    {
                                                                                        product.size_choices.map((size, i) => <option value={size[0]} key={i}>{size[1]}</option>)
                                                                                    }
                                                                                </React.Fragment>
                                                                            </select>
                                                                        </div>
                                                                    }
                                                                    <div className="col">
                                                                        <select className="form-select" name='quantity' >
                                                                            <option value={1} defaultValue>1</option>
                                                                            {
                                                                                [...Array(product.stock)].map((x, i) => {
                                                                                    if (i === 0) return null;
                                                                                    if (i > 4) return null;
                                                                                    else return <option value={i + 1} key={i}>{i + 1}</option>
                                                                                })
                                                                            }
                                                                        </select>
                                                                    </div>
                                                                </div>
                                                                <div className="row ">
                                                                    <div className="col my-3">
                                                                        <button type="submit" className="btn-primary btn w-100 d-inline"><i className="bi bi-cart3" /> Add to cart</button>
                                                                    </div>
                                                                </div>
                                                            </form>
                                                            :
                                                            null
                                                        }
                                                    </React.Fragment>
                                                }
                                            </React.Fragment>
                                        }
                                        {!isAuthenticated && <Link className='btn btn-primary btn-sm' to="/auth/login/"> Sign in to add in cart <i className="bi bi-box-arrow-in-up-right ms-1" /></Link>}
                                    </div>


                                    <div className="accordion" id="accordionExample">

                                        <div className="accordion-item">
                                            <h2 className="accordion-header" id="headingOne">
                                                <button className="accordion-button d-inline" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                                    <i className="bi bi-info-circle-fill fs-4" /> Product info
                                                </button>
                                            </h2>
                                            <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                                                <div className="accordion-body">{product.description}</div>
                                            </div>
                                        </div>
                                        <div className="accordion-item">
                                            <h2 className="accordion-header" id="headingTwo">
                                                <button className="accordion-button collapsed d-inline" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                                                    <i className="bi bi-stars text-primary fs-3" /> Coming soon
                                                </button>
                                            </h2>
                                            <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
                                                <div className="accordion-body">
                                                    Installment payments (installments) refer to the option to pay for purchases over time by dividing the purchase amount into smaller equal payments. Consumers will typically use installments to buy medium and large ticket items (e.g. electronics, furniture).
                                                </div>
                                            </div>
                                        </div>
                                        <div className="my-3">
                                            <span className='me-2'>Share:</span>
                                            {
                                                product &&
                                                <React.Fragment>
                                                    <TwitterShare url={`${localStorage.getItem('host')}/shop/#/shop/${product.slug}`} />
                                                    <FacebookShare url={`${localStorage.getItem('host')}/shop/#/shop/${product.slug}`} />
                                                </React.Fragment>
                                            }

                                        </div>

                                    </div>


                                </div>
                            </div>
                            {
                                reviews.length > 0 &&
                                <div className="row product__extra">
                                    <ProductInfo />
                                </div>
                            }
                        </div>
                    }
                </section>
            </React.Fragment >
        )
    }
}

const Product = (props) => {
    const navigate = useNavigate()
    const { productID } = useParams()
    const location = useLocation()
    const properties = Utils.objectUpdate(props, {
        navigate, productID, location
    })

    return <ProductChild {...properties} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Product);


