import React from "react";

import { TwitterButton, TwitterCount } from "react-social";

class TwitterShare extends React.Component {
    render() {
        const { url } = this.props;
        console.log("Twitter", url);
        let appId = "23809417"
        return (
            <TwitterButton url={url} appId={appId} className="btn btn-sm btn-outline-primary  me-2 my-3 px-lg-3">
                <TwitterCount url={url} />
                <i className="bi bi-twitter mx-2"></i>Twitter
            </TwitterButton>
        );
    }
}

export default TwitterShare;