import React, { Component } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';
import redux from '../../ReduxMapper';
import { connect } from 'react-redux';
import component from '../../Components';
import Api, { APICall } from '../../Api';
import ProductItem from './products/ProductItem';
import { EmptyProducts } from './products/EmptyProducts';
import main from '../../Main';

/**
*
* @mwangihub
* @summary Cart Component
* @class ProductChild
* @extends {Component}
* @param {object} props From redux => redux Mapper
* @return {JSX} return JSX <CartItemRow/>  
*
*/
class ProductsChild extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cached_products: null,
            cartegory: null,
            title: null,
        }
    }
    getitemsCategory = () => {
        APICall(Api.itemsCategory(), response => {
            if (response.data) {
                this.setState({ cartegory: response.data })
            }
        }, error => {
            console.log(error)
        }, 'fetching categories')
    }

    componentDidMount() {
        this.props.getHomeProducts();
        this.getitemsCategory();
    }

    setStateWithCartegory = e => {
        e.preventDefault();
        let cartegory = e.currentTarget.getAttribute('data-filter');
        if (cartegory === "all") {
            this.setState({ cached_products: this.props.products.homeProducts })
            return
        }
        const filteredProducts = this.props.products.homeProducts.filter(p => p.category_name === cartegory)
        this.setState({ cached_products: filteredProducts, title: cartegory })
    }


    render() {

        const { authentication: { isAuthenticated }, products: { homeProductsError, homeProductsLoader, homeProducts }, cartProducts: { cartItems } } = this.props;
        const { cached_products, cartegory, title } = this.state;

        return (
            <React.Fragment>
                <component.BreadCrumb
                    title={"Shopping grid"}
                    current={"Shop"}
                    links={[{ name: 'Home', to: '/' },]}
                />
                <section style={{ padding: "0px", marginBotton: "50px" }} className="product_list">
                    <div className="container pb-5 mb-2 mb-md-4">
                        <div className="row">

                            {/* Side page */}
                            <div className="col-lg-3 mt-5" data-aos="fade-up" data-aos-delay="400" >
                                {cartegory &&
                                    <div className="offcanvas offcanvas-start shadow-lg rounded-3 border-0 py-1" tabIndex="-1" id="filtersidebar" style={{ maxWidth: "23rem" }}>
                                        <div className="offcanvas-header align-items-center shadow-sm">
                                            <h4 className="offcanvas-title" id="filtersidebarLabel">Product filter</h4>
                                            <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                                        </div>
                                        <div className="offcanvas-body px-lg-4 py-lg-3">
                                            <h5 className="my-3 border-bottom pb-3">Categories</h5>
                                            <h5 className="small d-lg-none d-md-block d-sm-block my-3 pb-3">
                                                {cached_products &&
                                                    <React.Fragment>
                                                        {cached_products.length < 1 && <span className='text-danger'> No products match that filter. <span className="text-capitalize fw-bold">"{title}"</span> </span>}
                                                    </React.Fragment>
                                                }

                                            </h5>
                                            <div className="accordion" id="filterparent">
                                                <div className="accordion-item border-bottom pb-4" >
                                                    <h2 className="accordion-header fs-6 fw-bold">
                                                        <a className=" text-secondary" href="." data-filter='all' onClick={e => this.setStateWithCartegory(e)} data-bs-toggle="offcanvas" data-bs-target="#filtersidebar">
                                                            All products
                                                        </a>
                                                    </h2>
                                                </div>
                                                {
                                                    cartegory.map((category, i) => {
                                                        return (
                                                            <div className="accordion-item" key={i}>
                                                                <h2 className="accordion-header" id={`${category.category}${i}`}>
                                                                    <a className={`accordion-button text-capitalize ${i === 0 ? '' : 'collapsed'}`} data-bs-toggle="collapse" data-bs-target={`#${category.category}`} href=".">
                                                                        {category.category}
                                                                    </a>
                                                                </h2>
                                                                <div id={`${category.category}`} className={`accordion-collapse collapse ${i === 0 ? 'show' : ''}`} data-bs-parent="#filterparent">
                                                                    <div className="accordion-body p-0 ps-2">
                                                                        <ul className="list-unstyled">
                                                                            {
                                                                                category.sub_categories.map((sub, x) => {
                                                                                    return (
                                                                                        <li key={x}>
                                                                                            <React.Fragment>
                                                                                                <a className='d-lg-none' href="/" data-filter={`${sub.sub_category}`} onClick={e => this.setStateWithCartegory(e)} data-bs-toggle="offcanvas" data-bs-target="#filtersidebar">{sub.sub_category}</a>
                                                                                                <a className='d-lg-block d-none' href="/" data-filter={`${sub.sub_category}`} onClick={e => this.setStateWithCartegory(e)} >{sub.sub_category}</a>
                                                                                            </React.Fragment>
                                                                                        </li>
                                                                                    )
                                                                                })
                                                                            }

                                                                        </ul>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        )
                                                    })
                                                }
                                            </div>
                                        </div>
                                    </div>
                                }

                            </div>

                            {/* Main Page */}
                            <div className='col-lg-9 mt-5 home_products'>
                                <div className="text-center">
                                    <div className="section-header mb-5">
                                        <h2 className='text-capitalize'>Trending products</h2>
                                    </div>
                                </div>

                                {
                                    homeProductsError &&
                                    <div className="d-flex justify-content-center mt-2">
                                        <div className="col-lg-6 col-md-8 col-sm-12">
                                            <div className="alert alert-danger">{homeProductsError}</div>
                                        </div>
                                    </div>
                                }
                                {homeProductsLoader && <div id="preloader" />}
                                {homeProducts &&
                                    <React.Fragment>
                                        {homeProducts.length > 0 &&
                                            <div className="row gy-4">
                                                {!cached_products ?
                                                    <React.Fragment >
                                                        {homeProducts.map(product => <ProductItem product={product} key={product.id} className={`col-md-4 col-sm-6`} />)}
                                                    </React.Fragment> :
                                                    <React.Fragment >
                                                        {cached_products.length > 0 ?
                                                            cached_products.map(product => <ProductItem product={product} key={product.id} className={`col-md-4 col-sm-6`} />)
                                                            :
                                                            <React.Fragment>
                                                                <div className="alert alert-primary fade show">
                                                                    No products match that filter. <span className="text-capitalize text-primary fw-bold">"{title}"</span>
                                                                </div>
                                                                {homeProducts.map(product => <ProductItem product={product} key={product.id} className={`col-md-4 col-sm-6`} />)}
                                                            </React.Fragment>
                                                        }
                                                    </React.Fragment>
                                                }
                                            </div>
                                        }
                                    </React.Fragment>
                                }
                                {!homeProducts && <EmptyProducts />}
                            </div>

                        </div>
                    </div>
                </section >

                <div className="mobile-menu">
                    <div className="d-table table-layout-fixed w-100">
                        <span className="d-table-cell mobile-menu-item cursor-p" data-bs-toggle="offcanvas" data-bs-target="#filtersidebar">
                            <span className="mobile-menu-icon">
                                <i className="bi bi-funnel-fill" />
                            </span>
                            <span className="mobile-menu-label">Filters</span>
                        </span>

                        <Link className="d-table-cell mobile-menu-item" to={isAuthenticated ? `/profile/dashboard` : `/auth/login/`}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-person-circle" />
                            </span>
                            <span className="mobile-menu-label">Account</span>
                        </Link>


                        <a className="d-table-cell mobile-menu-item" href="." onClick={e => main.mobileNavToogleDefault(e)}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-list" />
                            </span>
                            <span className="mobile-menu-label">Menu</span>
                        </a>
                        {isAuthenticated &&
                            <Link className="d-table-cell mobile-menu-item" to="/cart/cart-view/">
                                <span className="mobile-menu-icon">
                                    <i className="bi bi-cart2" />
                                    {cartItems && <span className="badge bg-primary rounded-pill ms-1 fs-sm">{cartItems.items.length}</span>}
                                </span>
                                {cartItems && <span className="mobile-menu-label">Kshs.{(cartItems.order_total).toFixed(2)}</span>}
                            </Link>
                        }
                    </div>
                </div>
            </React.Fragment >
        )
    }
}

const Products = (props) => {
    const navigate = useNavigate()
    const { productID } = useParams()
    const location = useLocation()
    const properties = {
        navigate, productID, location
    }

    return <ProductsChild {...properties} {...props} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Products);



const side = props => {
    return <div className="sidebar_col ps-lg-4">
        <div className="sidebar-item search-form">
            <h5 className="sidebar-title">Search</h5>
            <form action="" className="mt-3">
                <input type="text" />
                <button type="submit">
                    <i className="bi bi-search"></i>
                </button>
            </form>
        </div>
        <div className="sidebar-item categories">
            <h5 className="sidebar-title">Categories</h5>
            <ul className="mt-3">
                <li><a href="/" data-filter="all" onClick={e => this.setStateWithCartegory(e)}>All</a></li>
                <li><a href="/" data-filter="men's clothing" onClick={e => this.setStateWithCartegory(e)}>Men's clothing</a></li>
                <li><a href="/" data-filter="women's clothing" onClick={e => this.setStateWithCartegory(e)}>Women's clothing </a></li>
                <li><a href="/" data-filter="electronics" onClick={e => this.setStateWithCartegory(e)}>Electronics </a></li>
                <li><a href="/" data-filter="jewelery" onClick={e => this.setStateWithCartegory(e)}>Jewelery </a></li>
            </ul>
        </div>
        <div className="sidebar-item tags d-none">
            <h5 className="sidebar-title">Tags</h5>
            <ul className="mt-3">
                <li><a href="/">App</a></li>
                <li><a href="/">IT</a></li>
                <li><a href="/">Business</a></li>
                <li><a href="/">Mac</a></li>
            </ul>
        </div>
    </div>
}