import React, { Component } from 'react'
class ProductInfo extends Component {
    constructor(props) {
        super(props)
        this.tabs = React.createRef();
        this.dataPane = React.createRef();
        this.setCurrentTab = this.setCurrentTab.bind(this);
    }
    setCurrentTab = e => {
        const tabs = [...this.tabs.current.children];
        const dataPane = [...this.dataPane.current.children];
        const element = e.target;
        tabs.forEach(tab => {
            tab.childNodes[0].classList.remove('active')
        });
        dataPane.forEach(pane => {
            const targetPane = pane.classList;
            targetPane.contains(element.id) ?
                targetPane.add('active', 'show') :
                targetPane.remove('active', 'show');
        });
        element.classList.contains('active') ?
            element.classList.remove('active') :
            element.classList.add('active');
    }
    render() {
        return (
            <>
                <div className="col-md-12 my-3">
                    <ul className="nav nav-tabs" ref={this.tabs}>
                        <li className="nav-item">
                            <span className="nav-link"
                                onClick={e => this.setCurrentTab(e)}
                                id='manufacturer'
                            >
                                Manufacturer
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link"
                                onClick={e => this.setCurrentTab(e)}
                                id='reviews'
                            >
                                Reviews
                            </span>
                        </li>
                    </ul>
                </div>

                <div className="tab-content" ref={this.dataPane}>
                    <div className="manufacturer tab-pane ">
                        <div className="p-4">
                            <h3 className="mb-4">Manufactured By Liquor Store</h3>
                            <p>On her way she met a copy. The copy warned the Little Blind Text, that where it came from it would have been rewritten a thousand times and everything that was left from its origin would be the word "and" and the Little Blind Text should turn around and return to its own, safe country. But nothing the copy said could convince her and so it didnt take long until a few insidious Copy Writers ambushed her, made her drunk with Longe and Parole and dragged her into their agency, where they abused her for their.</p>
                        </div>
                    </div>
                    
                    <div className="reviews tab-pane ">
                        <div className="row p-4">
                            <div className="col-md-7">
                                <h3 className="mb-4">23 Reviews</h3>
                                <div className="review">
                                    <div className="user-img" style={{backgroundImage: `url(https://technext.github.io/liquorstore/images/person_1.jpg)`}}/>
                                    <div className="desc">
                                        <h4>
                                            <span className="text-left">Jacob Webb</span>
                                            <span className="text-right">25 April 2020</span>
                                        </h4>
                                        <p className="star pt-3">
                                            <span>
                                                <i className="bi bi-star-fill"></i>
                                                <i className="bi bi-star-fill"></i>
                                                <i className="bi bi-star-fill"></i>
                                                <i className="bi bi-star-fill"></i>
                                                <i className="bi bi-star-fill"></i>
                                            </span>
                                        </p>
                                        <p>When she reached the first hills of the Italic Mountains, she had a last view back on the skyline of her hometown Bookmarksgrov</p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 ms-lg-5">
                                <div className="rating-wrap">
                                    <h3 className="mb-4">Give a Review</h3>
                                    <p className="star">
                                        <span>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            (98%)
                                        </span>
                                        <span>20 Reviews</span>
                                    </p>
                                    <p className="star">
                                        <span>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            <i className="bi bi-star-fill"></i>
                                            (85%)
                                        </span>
                                        <span>10 Reviews</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}
export default ProductInfo