import React from 'react'
import { useEffect } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../../Api';
import redux from '../../../ReduxMapper';

const ProductItem = props => {
    const { product, className, localCartUpdated, localHomeProductsUpdate, authentication: { isAuthenticated }, products: { homeProductsError, homeProductsLoader, homeProducts }, } = props;

    useEffect(() => {
        // console.log(homeProductsError, homeProductsLoader,)
    }, [homeProductsError, homeProductsLoader,])

    const updateItems = (item) => {
        const updatedProducts = homeProducts.map(obj => {
            if (obj.id === item.id) return { ...obj, ...item }
            return obj;
        });
        localHomeProductsUpdate(updatedProducts);
    }

    const handleAddtoCart = e => {
        let size, quantity = parseInt(e.target.quantity.value), slug = product.slug;
        if (e.target.size) {
            size = e.target.size.value
        }
        APICall(Api.addToCart({ size, quantity, slug }), response => {
            if (response.data.item) {
                updateItems(response.data.item)
                localCartUpdated(response.data.order);
            }
        })
    }

    const increaseQuantity = () => APICall(Api.addItemsToCart(product.slug), response => {
        if (response.data.item) {
            localCartUpdated(response.data.order);
            updateItems(response.data.item)
        }
    })


    const decreaseQuantity = () => APICall(Api.reduceFromCart(product.slug), response => {
        if (response.data.item) {
            localCartUpdated(response.data.order);
            updateItems(response.data.item);
        }
    })


    const deleteFromCart = () => APICall(Api.deleteFromCart(product.slug), response => {
        if (response.data.item) {
            localCartUpdated(response.data.order);
            updateItems(response.data.item)
        }
    })

    return (
        <div className={`${className} px-2 mb-4 `} key={product.id}>
            <div className="card product-card border-0">
                {isAuthenticated &&
                    <React.Fragment>
                        {product.cart_count &&
                            <button className="btn-action-view btn-sm" data-bs-toggle="tooltip" data-bs-placement="left" title={`${product.cart_count}- In your cart`}>
                                <i className="bi bi-cart" />
                            </button>
                        }
                    </React.Fragment>
                }

                {product.item_images && product.item_images.length > 0 &&
                    product.item_images.map((img, i) => {
                        if (img.current)
                            return (
                                <Link to={`/shop/product/${product.slug}/`} className="card-img-top overflow-hidden" key={i}>
                                    <img src={`${img.image}`} alt="Product" />
                                </Link>
                            )
                        return null
                    })
                }

                <div className="card-body py-2">
                    <span className="product-meta d-block fs-xs pb-1" >{product.main_category_name}</span>
                    <h3 className="product-title">
                        <Link to={`/shop/product/${product.slug}/`}>{product.title}</Link>
                    </h3>
                    <div className="d-flex flex-column">
                        <div className="product-price">
                            {product.discounted_price > 0 && <span >ksh.{product.discounted_price} ({product.stock} items)</span>}
                            {product.discounted_price > 0 && <del className=" text-muted ms-2">ksh.{product.price}</del>}
                            {product.discounted_price === 0 && <span >ksh.{product.price} ({product.stock} items)</span>}
                        </div>
                        <div className="star-rating">
                            {/* to implement */}
                            <i className="star-rating-icon bi bi-star-fill active"></i>
                            <i className="star-rating-icon bi bi-star-fill active"></i>
                            <i className="star-rating-icon bi bi-star-fill active"></i>
                            <i className="star-rating-icon bi bi-star-fill active"></i>
                            <i className="star-rating-icon bi bi-star-fill"></i>
                        </div>
                    </div>
                </div>
                <div className="card-body card-body-hidden">
                    {isAuthenticated &&
                        <div className="d-flex mb-2 flex-column align-items-center">
                            {!product.cart_count ?
                                <React.Fragment>
                                    {product.stock > 0 ?
                                        <form className='w-100' onSubmit={e => {
                                            e.preventDefault();
                                            handleAddtoCart(e);
                                        }}>
                                            <div className="row">
                                                {product.size_choices &&
                                                    <div className="col">
                                                        <select className="form-select form-select-sm" name='size'>
                                                            <option defaultValue>Select size</option>
                                                            <React.Fragment>
                                                                {
                                                                    product.size_choices.map((size, i) => <option value={size[0]} key={i}>{size[1]}</option>)
                                                                }
                                                            </React.Fragment>
                                                        </select>
                                                    </div>
                                                }
                                                <div className="col">
                                                    <select className="form-select form-select-sm" name='quantity' >
                                                        <option value={1} defaultValue>1</option>
                                                        {
                                                            [...Array(product.stock)].map((x, i) => {
                                                                if (i === 0) return null;
                                                                if (i > 4) return null;
                                                                else return <option value={i + 1} key={i}>{i + 1}</option>
                                                            })
                                                        }
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="row ">
                                                <div className="col my-3">
                                                    <button type="submit" className="btn-primary btn-sm btn w-100 d-inline"><i className="bi bi-cart3" /> Add to cart</button>
                                                </div>
                                            </div>
                                        </form>
                                        :
                                        <button className="btn btn-sm btn-secondary" disabled>Out of stcok</button>
                                    }
                                </React.Fragment>
                                :
                                <React.Fragment>
                                    {product.stock > 0 ?
                                        <div className='d-flex flex-column'>
                                            <div className="input-group col-md-6 mb-3">
                                                <button className="btn btn-sm btn-outline-dark me-3 fw-bold" onClick={e => {
                                                    if (product.stock > 0) {
                                                        increaseQuantity()
                                                    }
                                                }} disabled={product.stock < 1}>
                                                    <i className="fs-5 bi bi-plus" />
                                                </button>
                                                <button className="btn btn-sm btn-outline-secondary px-5 text-black fw-bold" disabled>{product.cart_count}</button>
                                                <button className="btn btn-sm btn-outline-dark ms-3 fw-bold" onClick={e => decreaseQuantity()}>
                                                    <i className="fs-5 bi bi-dash" />
                                                </button>
                                            </div>
                                            <button className="btn btn-outline-danger btn-sm" onClick={e => deleteFromCart()}>Remove from cart</button>
                                        </div>
                                        :
                                        <div className="d-flex justify-content-between w-100">
                                            <button className="btn btn-sm btn-secondary" disabled>Out of stcok</button>
                                            <button className="btn btn-outline-danger btn-sm" onClick={e => deleteFromCart()}>Remove from cart</button>
                                        </div>

                                    }
                                </React.Fragment>
                            }
                        </div>

                    }
                    {/* <div className="text-center">
                        <Link to={`/shop/product/${product.slug}/`} className='btn btn-sm btn-light'>
                            <i className="bi bi-eye me-1" />View
                        </Link>
                    </div> */}
                </div>
            </div>
            <hr className="d-sm-none" />
        </div >
    )
}


export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(ProductItem)