
import SocialLogin from './SocialLogin';
import { gapi, loadAuth2 } from 'gapi-script'
import { connect } from 'react-redux';
import React, { useState, useEffect, useRef } from 'react';
import redux from '../../../ReduxMapper';
import Api, { APICall } from '../../../Api';
import { useNavigate } from 'react-router-dom';
import SetGoogleScript from './SetGoogleScript';
import jwt_decode from 'jwt-decode';


/**
 * @mwangihub
 * @summary Login using Google, extends from SocialLogin HOC
 * @param   {object} this.props  as props from API call in SocialLogin HOC
 * @return {JSX} returns GoogleSocialAuth JSX as GoogleButton. On success calls auth method to the backend with params received
 * NOTE: AIzaSyB2VxMpTHEPy9guOFkF0NEsMulqRwNrpuE
 * Google has migrated to new way of handling OAuth clients
 * July,2022
 * jwt_decode
 *  localhost:8000/api/user/google
 */
const SCOPES = "https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/userinfo.profile"
const GoogleSocialAuth = props => {
    const googleBtn = useRef()
    const { clientID, localAuthenticated, localAuthFail } = props;
    // const [user, setUser] = useState(null);
    const [tokenClient, setTokenClient] = useState();
    const status = SetGoogleScript(
        "https://accounts.google.com/gsi/client"
    );

    // const handleGoogleCallBackResponse = response => {
    //     // const decodedUser = jwt_decode(response.credential);
    //     // console.log(decodedUser);
    // }
    const getToken = () => {
        tokenClient.requestAccessToken();
    }
    useEffect(() => {
        if (status === "ready") {
            /* global google */
            if (google) {
                // google.accounts.id.initialize({
                //     client_id: clientID,
                //     callback: handleGoogleCallBackResponse
                // })
                // google.accounts.id.renderButton(googleBtn.current, {
                //     theme: "filled_blue",
                //     size: "large",
                //     shape: "rectangular",
                //     width: googleBtn.current.offsetWidth,
                //     height: googleBtn.current.offsetHeight,
                // });
                // google.accounts.id.prompt()
                setTokenClient(google.accounts.oauth2.initTokenClient({
                    client_id: clientID,
                    scope: SCOPES,
                    callback: tokenResponse => {
                        if (tokenResponse && tokenResponse.access_token) {
                            APICall(Api.authWithGoogle({ "access_token": tokenResponse.access_token }), success => {
                                let data = success.data;
                                if (data.key) {
                                    APICall(Api.getBuyerProfile(), success => {
                                        if (success.data.buyer_profile) {
                                            localAuthenticated({
                                                "profile": success.data.buyer_profile,
                                                "details": "Log in successful",
                                                "isAuthenticated": true,
                                            });
                                        };
                                    }, error => {

                                        console.log(error);
                                        // setState({ loading: false, error })
                                    }, 'retrieving buyer profile')
                                }
                            }, error => {
                                if (error.data) {
                                    let error_;
                                    if (typeof error.data === 'object') {
                                        error_ = error.data.non_field_errors[0]
                                    }
                                    localAuthFail({
                                        "profile": null,
                                        "details": error_,
                                        "isAuthenticated": false,
                                    });
                                    return
                                }
                                console.log(error)
                            }, "authenticating with google.")
                        };
                    }
                }))
            }
        }
    }, [clientID, status, localAuthenticated, localAuthFail])


    return (
        <div id="login-with-google">

            {status === "ready" &&
                <button type='button' className="w-100 py-2 mb-2 btn btn-outline-primary rounded-4" onClick={getToken}>
                    <i className="bi bi-google fs-5 me-2" />
                    Sign in with google
                </button>
            }
            <div className="border-primary d-flex justify-content-center my-3 w-100 d-none" ref={googleBtn} />
            {/* {user &&
                <div className="d-flex flex-column">
                    <span>{user.name}</span>
                    <img src={user.picture} className="img-thumbnail rounded-circle" alt="google" height="100" width="100" />
                    <span>{user.email}</span>
                </div>
            } */}
            <div className="my-2 d-none">
                <div className="alert alert-info alert-dismissible fade show" role="alert">
                    <ol>
                        <li> Google OAuth2.0 SDK <strong>(Google Identity Services)</strong> doesnt work since its set to deprecated after March 31, 2023.</li>
                        <li>The callback urls cannot be set in google developer console.</li>
                        <li>The new <strong>Google Identity Services</strong> is still under development by google teams.</li>
                    </ol>
                    <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                </div>
            </div>
        </div>
    )


}

const GoogleSocialLogin = SocialLogin(GoogleSocialAuth, {
    name: 'google'
})


export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(GoogleSocialLogin);
