import React from "react";

import { FacebookButton, FacebookCount } from "react-social";

class FacebookShare extends React.Component {
    render() {
        const { url } = this.props;
        let appId = "413942477518650"
        return (
            <FacebookButton url={url} appId={appId} className="btn btn-sm btn-outline-dark  me-2 my-3 px-lg-3">
                <FacebookCount url={url} />
                <i className="bi bi-facebook mx-2"></i>Facebook
            </FacebookButton>
        );
    }
}

export default FacebookShare