import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link, Outlet } from 'react-router-dom';
import DevInfo from '../DevInfo';
import redux from '../ReduxMapper';
import Navbar from './container/Navbar';
import { useNavigate } from 'react-router-dom';
import main from '../Main';

class ProfileComponent extends Component {
    componentDidMount() {
        document.querySelector("body").style.background = "#f6f9ff"
    }
    render() {

        const { authentication: { isAuthenticated, profile }, debug, authLogout, navigate, cartProducts: { cartItems } } = this.props;
        const handleLogout = () => {
            navigate("/");
            document.querySelectorAll(".modal-backdrop").forEach(element => {
                element.remove();
            });
            document.querySelector("body").removeAttribute('style')
        }
        return (
            <React.Fragment>
                <DevInfo {...doc} {...debug} />
                <Navbar />
                <main id="main" className="main">
                    <div className="modal fade" id="logOutModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content">
                                <div className="modal-header border-0">
                                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                </div>
                                <div className="modal-body">
                                    Are you sure you want to Sign out?
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary btn-sm" data-bs-dismiss="modal">Cancel</button>
                                    <button type="button" className="btn btn-primary btn-sm" onClick={e => { authLogout(handleLogout) }}>Sign out</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Outlet />
                </main>
                <div className="mobile-menu">
                    <div className="d-table table-layout-fixed w-100 shadow-lg">
                        <Link className="d-table-cell mobile-menu-item" to="/shop/products/">
                            <span className="mobile-menu-icon">
                                <i className="bi bi-house-fill" />
                            </span>
                            <span className="mobile-menu-label">Home</span>
                        </Link>
                        <a className="d-table-cell mobile-menu-item" href="." onClick={e => main.mobileNavToogleDefault(e)}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-shop-window" />
                            </span>
                            <span className="mobile-menu-label">Shop</span>
                        </a>
                        <Link className="d-table-cell mobile-menu-item" to={isAuthenticated ? `/profile/dashboard` : `/auth/login/`}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-person-circle" />
                            </span>
                            <span className="mobile-menu-label">Account</span>
                        </Link>
                        {isAuthenticated &&
                            <Link className="d-table-cell mobile-menu-item" to="/cart/cart-view/">
                                <span className="mobile-menu-icon">
                                    <i className="bi bi-cart2" />
                                    {cartItems && <span className="badge bg-primary rounded-pill ms-1 fs-sm">{cartItems.items.length}</span>}
                                </span>
                                {cartItems && <span className="mobile-menu-label">Kshs.{(cartItems.order_total).toFixed(2)}</span>}
                            </Link>
                        }
                    </div>
                </div>

            </React.Fragment>
        )
    }
}
const Profile = props => {
    return (<ProfileComponent {...props} navigate={useNavigate()} />);
};

export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Profile);

const doc = {
    title: "Customer Profile page",
    description: [
        ["1. USER DASHBOARD"],
        ["This is the customers dashboard"],
        ["Less is done here"],
        ["TODO:"],
        ["          - Viewing uncompleted order"],
        ["          - Tracking placed order"],
        ["          - Request refund"],
        ["          - Shipping/Billing Address update"],
        ["          - User Details update"],
        ["          - Contact for help"],
        ["--------------------------------------------------------------------------------------"],
        ["2. ADMIN DASHBOARD"],
        ["  This is the ADMIN dashboard"],
        ["  NOT YET IMPLEMENTED"],
        ["  TODO:"],
        ["          - Do ALL customer can do."],
        ["          - CRUD for; items, orders, cartItems, users, e.t.c"],
        ["--------------------------------------------------------------------------------------"],

    ]
}