import React, { Component } from 'react';
// import FacebookLogin from 'react-facebook-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import { connect } from 'react-redux';
import SocialLogin from './SocialLogin';
import redux from '../../../ReduxMapper';
import Api, { APICall } from '../../../Api';
import { useNavigate } from 'react-router-dom';
import SetGoogleScript from './SetGoogleScript';
import jwt_decode from 'jwt-decode';

/**
 * @mwangihub
 * Login using Facebook, extends from SocialLogin HOC
 * @param   {object} this.props  as props from API call in SocialLogin HOC
 */
class FaceBookSocialLogin extends Component {
    componentDidMount() {
        // console.log(this.props)
    }
    render() {
        const { clientID, authLoader, authFail, authStart } = this.props;

        const responseFacebook = (response) => {
            const { accessToken, email, name, picture, id } = response;
            console.log(response);
            APICall(Api.authWithFacebook({ "access_token": accessToken, 'code': id }), success => {
                console.log(success)
            }, error => { console.log(error) }, 'authenticating with facebook.')
        };
        const componentClicked = event => console.log(event);
        return (
            <div className="">
                {authLoader &&
                    <div className="d-flex justify-content-center my-2">
                        <div className="spinner-border text-success text-center" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div>
                    </div>}

                {!authLoader && clientID &&
                    <div className="googleBtn">

                        <FacebookLogin
                            appId="413942477518650"
                            autoLoad={false}
                            callback={responseFacebook}
                            render={renderProps => (
                                <button onClick={renderProps.onClick} className="w-100 py-2 mb-2 btn btn-outline-dark rounded-4" type='button'>
                                    <i className="bi bi-facebook fs-5 me-2" />
                                    Signin with Facebook
                                </button>
                            )}
                        />


                        {/* <FacebookLogin
                            appId="1026941931509651"
                            autoLoad={false}
                            fields="name,email,picture"
                            callback={responseFacebook}
                            onClick={componentClicked}

                            cssClass="w-100 py-2 mb-2 btn btn-primary rounded-4"
                        /> */}
                    </div>}
            </div>
        )
    }
}
const FaceBookButton = SocialLogin(FaceBookSocialLogin, {
    name: 'facebook'
})

export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(FaceBookButton);
