import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom'
import component from '../Components';
import DevInfo from '../DevInfo';
import main from '../Main';
import redux from '../ReduxMapper';
import Utils from '../utils/utils';

class CartComponent extends Component {
    state = {}
    componentDidMount() {
        this.props.getCart()
    }
    static getDerivedStateFromProps(props, state) {
        try {
            const isNull = props.cartProducts.cartItems === null
            if (!isNull) {
                if (Object.entries(props.cartProducts.cartItems).length === 0)
                    props.navigate("/shop/products")
            }
        } catch (error) { console.log(error) }
        return null;
    }
    render() {
        const { authentication: { isAuthenticated, profile }, debug, cartProducts: { cartItems } } = this.props;
        return (
            <React.Fragment>
                <DevInfo {...doc}{...debug} />
                <component.Nav1 {...this.props} />

                <Outlet />


                <div className="mobile-menu">
                    <div className="d-table table-layout-fixed w-100">
                        <Link className="d-table-cell mobile-menu-item" to="/shop/products">
                            <span className="mobile-menu-icon">
                                <i className="bi bi-house-fill" />
                            </span>
                            <span className="mobile-menu-label">Home</span>
                        </Link>

                        <Link className="d-table-cell mobile-menu-item" to={isAuthenticated ? `/profile/dashboard` : `/auth/login/`}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-person-circle" />
                            </span>
                            <span className="mobile-menu-label">Account</span>
                        </Link>


                        <a className="d-table-cell mobile-menu-item" href="." onClick={e => main.mobileNavToogleDefault(e)}>
                            <span className="mobile-menu-icon">
                                <i className="bi bi-list" />
                            </span>
                            <span className="mobile-menu-label">Menu</span>
                        </a>
                        {isAuthenticated &&
                            <Link className="d-table-cell mobile-menu-item" to="/cart/cart-view/">
                                <span className="mobile-menu-icon">
                                    <i className="bi bi-cart2" />
                                    {cartItems && <span className="badge bg-primary rounded-pill ms-1 fs-sm">{cartItems.items.length}</span>}
                                </span>
                                {cartItems && <span className="mobile-menu-label">Kshs.{(cartItems.order_total).toFixed(2)}</span>}
                            </Link>
                        }
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
const Cart = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const properties = Utils.objectUpdate(props, {
        navigate, location
    })
    return <CartComponent {...properties} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Cart);

