import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import redux from '../../ReduxMapper'
import { connect } from 'react-redux';
import Api, { APICall } from '../../Api';

class Hero extends Component {
    state = {
        loading: false,
        error: null,
        data: null
    }
    fetchTrendingItems = () => {
        this.setState({ loading: true, error: null })
        APICall(Api.trendingItems(), response => {
            if (response.data) {
                this.setState({ loading: false, error: null, data: response.data })
            }
        }, error => {
            this.setState({ loading: false, error: JSON.stringify(error) })
        }, 'fetching trending items')
    }
    componentDidMount() {
        this.fetchTrendingItems()
    }


    render() {
        const { loading, error, data } = this.state;
        return (
            <main className='shop-hero'>
                <section id="hero" className="hero d-flex align-items-center">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-4">
                                <h2 data-aos="fade-up">Focus On What Matters</h2>
                                <blockquote data-aos="fade-up" data-aos-delay="100">
                                    <p>Take a risk and keep testing, because what works today won't work tomorrow, but what worked yesterday may work again.</p>
                                </blockquote>
                                <div className="d-flex">
                                    <Link to="/shop/products/" className="btn-get-started">Shop now</Link>
                                </div>

                            </div>
                        </div>
                    </div>
                </section>
                {!loading && data && !error &&
                    <section className="container position-relative pt-3 pt-lg-0 pb-5" id="popular-categories">
                        <div className="row">
                            <div className="col-xl-8 col-lg-9">
                                <div className="card border-0 shadow">
                                    <div className="card-body px-3 pb-0">
                                        <div className="row g-0 ps-1">
                                            {data.map((item, i) => {
                                                return (
                                                    <div className="col-sm-4 px-2 mb-3" key={i}>
                                                        <a className="d-block text-center text-decoration-none me-1" href="." onClick={e => e.preventDefault()}>
                                                            <img className="d-block rounded mb-3 img-fluid" src={`${localStorage.getItem('host')}${item.get_item_images[0]}`} alt="Men" />
                                                            <h3 className="fs-6 pt-1 mb-0 text-secondary fw-bold text-capitalize">{item.get_main_category}</h3>
                                                        </a>
                                                    </div>
                                                )
                                            })}


                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                }
            </main>
        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Hero);